import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import image from './image/gamysh.jpg';
import image1 from './image/hotel.jpg'
import image2 from './image/hotel1.jpg'
import image3 from './image/hotel2.jpeg'
import logo1 from './image/logo_gray.png'
import { Icon } from 'react-icons-kit'
import {smallDown} from 'react-icons-kit/entypo/smallDown';
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import { getCategory,getFilters, getToken, getUser } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";

const Navbar = lazy(() => import('./Navbar'))
const Search = lazy(() => import('./Search'))
const Footer = lazy(() => import('./Footer'))


function Kommercheskaya(){
  const { lang, dispatchLang } = useContext(LangContext);

    const dispatch = useDispatch();
    const history = useHistory();

    const categories = useSelector(getCategory);
    const [ready_filter, setReady_filter] = useState();
    // const token = useSelector(getToken);
    const user = useSelector(getUser);


    

    const [products, setProducts] = useState();
    const [type, setType] = useState(1);
    let dil;
    if (lang=='ru') 
    {dil='ru'} 
    else 
    {dil='tm'}

    useEffect(() => {
        console.log(type);
       dispatch(fetchData({
        url:`${dil}/all-real-estates?page=0&limit=16&main_type_id=${type}&user_id=${user?.id ? user?.id : ``}`,
        token: '',
        action:(response)=>{
            if(response.success){
                // console.log("get_data ",response)
                // console.log(response.data.rows[0].real_estates_all[0])
                setProducts(response.data.rows[0].real_estates_all);
                // console.log(products)
            } else {
                console.log("error_data ",response);
                }
            }
        }))
    }, [type,dil]);
    useEffect(() => {
        dispatch(fetchData({
            url:`${dil}/commerce-filter`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("commerce ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setReady_filter(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);
    
    // console.log(ready_filter);
    return(
        <main>
            <Navbar />
            <Search />
            
            <div className="main py-8 bg-gray-100">
                <div className="main1">
                <h1 className="text-xl sm:text-3xl font-medium mb-4">{Lang1[lang].good_url}</h1>
                    <div className="flex flex-col md:flex-row sm:justify-between">
                    <div className="categories">
                    {ready_filter?.length > 0 ?
                            ready_filter[0]?.estates?.slice(0,8).map((filter_1)=>(
                                //     <div onClick={()=>{ 
                                //         history.push(`/products/${ready_filter[0]?.category_id}/${filter_1?.type_id}`)
                                //     }}  className="cursor-pointer">
                                //     <span className="text-sm text-blue-600">{}</span>
                                //     <span className="text-xs text-gray-500 ml-3">{filter_1?.count}</span>
                                // </div>
                                <div className="cate_box1 cursor-pointer mb-4 sm:mb-0"
                                 style={{ backgroundImage: `url(https://gamysh.com/${filter_1.destination ? `${filter_1.destination}` : `uploads/images/hotel.jpg`})` }}
                                 >
                                <div className="box_text1 p-6">
                                    <p className="text-2xl text-white font-medium cursor-pointer">{filter_1?.name}</p>
                                    <div className="flex mt-6">
                                        <div  onClick={()=>{ 
                                        history.push(`/products/${ready_filter[0]?.category_id}/${filter_1?.type_id}`)
                                    }} className="cursor-pointer flex flex-col">
                                            <span className="text-sm text-white hover:border-b">Аренда</span>
                                            {/* <span className="text-xs text-gray-300 mt-1">26 296</span> */}
                                        </div>
                                        <div onClick={()=>{ 
                                        history.push(`/products/${ready_filter[0]?.category_id}/${filter_1?.type_id}`)
                                    }} className="cursor-pointer flex flex-col ml-14">
                                            <span className="text-sm text-white hover:border-b">Продажа</span>
                                            {/* <span className="text-xs text-gray-300 mt-1">6 296</span> */}
                                        </div>
                                    </div>
                                </div>
                            </div>
                                ))
                                :
                                null
                                }
                            
                        
                       
                    </div>
                    <div className="sub_category">
                        {ready_filter?.length > 0 ?
                            <div className="w-full flex flex-col">
                            <p className="flex items-center font-medium pt-3 text-lg">Продается</p>
                                {ready_filter[0]?.estates?.slice(0,8).map((filter_1)=>(
                                    <div onClick={()=>{ 
                                        history.push(`/products/${ready_filter[0]?.category_id}/${filter_1?.type_id}`)
                                    }}  className="cursor-pointer">
                                    <span className="text-sm text-blue-600">{filter_1?.name}</span>
                                    <span className="text-xs text-gray-500 ml-3">{filter_1?.count}</span>
                                </div>
                                ))}
                             </div>                           
                            :
                            null
                            }
                            {ready_filter?.length > 0 ?
                            <div className="w-full flex flex-col pt-3">
                            <p className="flex items-center font-medium text-lg">Сдается в аренду</p>
                            {ready_filter[1]?.estates?.slice(0,8).map((filter_1)=>(
                                <div onClick={()=>{ 
                                    history.push(`/products/${ready_filter[1]?.category_id}/${filter_1?.type_id}`)
                                }}  className="cursor-pointer">
                                        <span className="text-sm text-blue-600">{filter_1?.name}</span>
                                        <span className="text-xs text-gray-500 ml-3">{filter_1?.count}</span>
                                    </div>
                            ))}
                            </div>
                            :
                            null
                        }
                        </div>
                    </div>
                </div>
            </div>
            <div className="main py-8 bg-gray-100">
                <div className="main1">
                <h1 className="text-xl sm:text-3xl font-medium mb-4">{Lang1[lang].hom15}</h1>
                <div className="flex items-center mb-5">
                    <p className="text-lg sm:text-xl font-medium mr-4">{Lang1[lang].hom16}</p>
                    {/* {categories?.map((cate)=>( */}
                        {categories[0]?.main_types?.map((cate_main)=>(
                            type==cate_main.id
                            ?
                            <p className="text-lg mr-4 cursor-pointer border-b-4 border-blue-600" onClick={() =>{
                                setType(cate_main.id);
                                // setLine(cate_main.id);
                                // console.log(line);
                            } } key={cate_main.id}>{cate_main?.name}</p>
                            :
                            <p className="text-lg mr-4 cursor-pointer" onClick={() =>{
                                setType(cate_main.id);
                                // setLine(cate_main.id);
                                // console.log(line);
                            } } key={cate_main.id}>{cate_main?.name}</p>
                        ))}
                    {/* ))} */}
                    {/* <p onClick={() => Count(1)} className=" text-lg mr-4 border-b-2 border-blue-400 cursor-pointer">Жилая</p> */}
                    {/* <p onClick={() => Count(2)} className="text-lg mr-4 cursor-pointer">Коммерческая</p> */}
                </div>
                <div className="cards">
                    {products?.map((item)=>(
                        <div onClick={()=> history.push(`/single/${item?.id}`) } className={`card_box cursor-pointer relative ${item?.vip_type_id === 1 ? "bg-yellow-100": "bg-white"}`} key={item.id}>
                            {
                                item?.vip_type_id === 1 ?
                                <div className='absolute top-1 left-2'>
                                    <img src='https://gamysh.com/uploads/images/vip.png' className='w-10' />
                                </div>
                                :
                                null
                            }
                            {item.images === null ? <div className="card_img flex justify-center items-center bg-gray-200"><img src='https://gamysh.com/uploads/images/logo_gray.png' className="img1"  /></div> : <img className="card_img" src={`https://gamysh.com/${item.images[0]?.destination}-large.webp`} />}
                            <p className="font-medium pl-2 pt-2 text-lg">{item?.price} <span className='text-sm'>TMT</span></p>
                            <p className="pl-2">{item?.real_estate_name}</p>
                            <p className="mb-4 pb-2 pl-2 sm:mb-2">{item.location}</p>
                        </div>
                    ))}
                    
                </div>
                </div>
            </div>
            <Footer />
        </main>

    )
}

export default Kommercheskaya;